.Search {
    display: grid;
    height: 100%;
}

.Search .searchForm {
    display: grid;
    grid-template-rows: repeat(5, 4vw) 1fr;
    grid-template-columns: auto 1fr;
    width: 80%;
    border-radius: 1vw;
    border: 0.2vw solid black;
    margin: auto auto 2vw;
    padding-top: 1.5vw;
    padding-bottom: 1.5vw;
    background-color: rgb(155, 202, 139);
}

.Search .searchForm * {
    margin: auto;
}

.Search .searchForm div {
    width: auto;
    font-size: 1.3vw;
    margin: auto 1vw auto 1.5vw;
}

.Search .searchForm input[type="text"] {
    width: 85%;
    font-size: 1.2vw;
    padding: 0.5vw;
    margin-left: 0;
}

.Search .searchForm select {
    width: 92%;
    font-size: 1.2vw;
    padding: 0.5vw;
    margin-left: 0;
}

.Search .searchForm .searchInput {
    grid-column: 2;
    font-size: 2vw;
    margin: 1vw 3vw auto auto;
    width: auto;
    background: #009e0f;
    border: 0.15vw solid black;
}

.Search .searchForm .exportInput {
    grid-column: 2;
    font-size: 2vw;
    margin: 1vw 3vw auto auto;
    width: auto;
    border: 0.15vw solid black;
}

.Search .searchForm .searchInput:hover {
    cursor: pointer;
}

@media (orientation: landscape) {
    .Search {
        grid-template-columns: 1fr 1fr 1fr;
        overflow: auto;
    }

    .Search caption {
        font-size: 1.5vw;
    }

    .Search tbody {
        max-height: calc(85vh - 6.5vw - 2.5vmax);
    }

    .Search td, th {
        width: 6.5vw;
        max-width: 6.5vw;
        font-size: 0.8vw;
        height: 1vw;
    }

    .Search td:last-child {
        width: 2vw;
    }

    .Search th:last-child {
        width: 3.1vw;
    }

    .infoTitle {
        font-size: 1.5vw;
        margin: 1vw 0 1vmin 0;
        text-align: center;
    }

    /*CSS for members array*/
    .Search .members {
        margin: 1vw auto auto;
        max-height: calc(90vh - 6.5vw - 2.5vmax);
    }

    .Search .MemberInfo {
        --default-font-size: 1.5vw;
        --name-font-size: 2.2vw;
        --telephone-font-size: 1.6vw;
        --input-label-font-size: 1.1vw;

        max-height: calc(90vh - 6.5vw - 2.5vmax);
    }

    .Search .MemberInfo .infoArea {
        width: 80%;
        border: 0.2vw solid black;
        border-radius: 1vw;
        margin: auto auto 2vw;
    }
}

@media (orientation: portrait) {
    .Search {
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 2fr 10fr;
    }

    .Search .searchArea {
        width: 90%;
        margin: 3vw auto auto;
    }

    /*CSS for members array*/
    .Search .members {
        grid-row: 2;
        grid-column: 1 / 3;
        margin: 3vmin auto auto;
    }

    .Search caption {
        font-size: 3vw;
    }

    .Search .members tbody {
        max-height: calc(90vh - 6.5vw - 2.5vmax - 65vw);
    }

    .Search .members td, .Search .members th {
        width: 24vw;
        max-width: 24vw;
        font-size: 2vw;
        height: 2.4vw;
    }

    .Search .members td:last-child {
        width: 6vw;
    }

    .Search .members th:last-child {
        width: auto;
    }

    /*CSS for Board array*/
    .Search .CA {
        grid-row: 1;
        grid-column: 1;
        margin: 3vw auto auto;
    }

    .Search .CA tbody {
        max-height: 27vh;
    }

    .Search .CA td, .Search .CA th {
        width: 8.5vw;
        max-width: 8.5vw;
        font-size: 1vw;
        height: 1.2vw;
    }

    .Search .CA td:last-child {
        width: 3vw;
    }

    .Search .CA th:last-child {
        width: 3vw;
    }

    .Search .MemberInfo {
        --default-font-size: 2.2vw;
        --name-font-size: 3vw;
        --telephone-font-size: 2.5vw;
        --input-label-font-size: 2vw;

        width: 95%;
        max-height: calc(90vh - 6.5vw - 2.5vmax);
        margin: 3vw auto auto;
    }

    .Search .infoTitle {
        font-size: 3vw;
        margin: 0 0 1vmin 0;
        text-align: center;
    }

    .Search .searchForm {}

    /*CSS for member info*/
    .Search .MemberInfo .infoArea {
        grid-template-rows: 2vw 4.5vw 4.5vw 4vw repeat(5, 2.5vw 5.1vw);
        width: 80%;
        border: 0.3vmin solid black;
        margin: auto 13% auto auto;
    }
}